import EventEmitter from 'eventemitter3';
import Singleton from '../clazz/Singleton';
import EventStore from './EventStore';
import Util from './Util';

/**
 * 该类对事件进行统一管理
 */
export default class EventController extends Singleton {
    /**
     * 初始化事件管理器
     */
    constructor() {
        super();

        this.eventEmitter = new EventEmitter();

        /**
         * 是否为严格模式
         * 严格模式下，事件名称必须在EventStore中注册，否则会报错
         * @type {Boolean}
         */
        this.strict = false;
    }

    /**
     * 添加事件
     * @param {string} name - 事件名称
     * @param {object} listener - 响应事件的函数
     * @param {object} context - 事件上下文
     */
    on(name, listener, context) {
        this._check(name);

        this.eventEmitter.on(name, listener, context);
    }

    /**
     * 添加事件
     * @param {string} name - 事件名称
     * @param {object} listener - 响应事件的函数
     * @param {object} context - 事件上下文
     */
    once(name, listener, context) {
        this._check(name);

        this.eventEmitter.once(name, listener, context);
    }

    /**
     * 删除事件
     * @param {string} name - 事件名称
     * @param {object} listener - 响应事件的函数,可选,如果不传则删除所有响应函数
     */
    off(name, listener) {
        this._check(name);

        if (listener) {
            this.eventEmitter.off(name, listener);
        } else {
            this.eventEmitter.removeAllListeners(name);
        }
    }

    /**
     * 触发指定事件
     * @param {string} name - 事件名称
     * @param {object} arg - 事件参数,可选
     */
    fire(name, arg) {
        this._check(name);

        this.eventEmitter.emit(name, arg);
    }

    _check(name) {
        if (Util.isUndefined(name)) {
            throw new Error('事件名称不能为空');
        }

        if (this.strict && !EventStore.contains(name)) {
            throw new Error('事件名称未注册');
        }
    }

    /**
     * 清空所有事件
     */
    clear() {
        this.eventEmitter.removeAllListeners();
    }
}



