import createDebug, { enable } from 'debug';
import Singleton from '../clazz/Singleton';

export default class Logger extends Singleton {
    constructor() {
        super();

        this.namespace = 'App::UtilLogger';
        this._updateDebug();
    }

    setNamespace(namespace) {
        this.namespace = namespace;
        this._updateDebug();
    }

    log(string) {
        this.debug(string);
    }

    _updateDebug() {
        enable(this.namespace);

        this.debug = createDebug(this.namespace);
    }
}



