import Base from './Base';

/**
 * 单例类的基类
 */
export default class Singleton extends Base {
    /**
     * 构造函数
     * @return {undefined}
     */
    constructor(options) {
        super(options);
    }

    /**
     * 销毁单例实例对象
     * @return {undefined}
     */
    destroy() {
        Singleton._singetons.delete(this.constructor);
    }

    /**
     * 获取单例对象的实例
     * @param  {...all} rest 参数
     * @return {class}  单例对象的实例
     */
    static getInstance(...rest) {
        let instance = null;
        if (!Singleton._singetons.has(this)) {
            instance = new this(...rest);

            Singleton._singetons.set(this, instance);
        }

        return Singleton._singetons.get(this);
    }

    /**
     * 记录所有实例化的单例子类
     * 改该属性只在基类中
     * @type {Map}
     */
    static _singetons = new Map();

    /**
     * 销毁所有单例类实例
     * @return {undefined}
     */
    static destroyAll() {
        if (this !== Singleton) {
            throw new Error('只有单例基类Singleton可以执行destroyAll方法。');
        }

        Singleton._singetons.forEach(item => {
            item.destroy();
        });

        // 防止子类的destroy方法未调用父类的destroy
        Singleton._singetons.clear();
    }
}



