import Singleton from './Singleton';

/**
 * 工厂类的基类
 */
export default class Factory extends Singleton {
    /**
     * 构造函数
     * @param  {object}     options 其他参数
     * @return {undefined}
     */
    constructor(options) {
        super(options);

        /**
         * 配置表
         * @type {Object}
         */
        this._kvMap = {};
    }

    /**
     * 将此实例对象添加到其他对象中
     * @param {object} obj 添加到的对象
     */
    addTo(obj) {
        obj[this.constructor.name] = this;
    }

    /**
     * 获取配置
     * @return {object} 键值对对象
     */
    getConfig() {
        return this._kvMap;
    }

    /**
     * 根据多个参数，拼接成key字符串
     * @param {String|Number} keyString 
     */
    createKey(...keyString) {
        return keyString.filter(it => !!it).join('-');
    }

    /**
     * 注册内容
     * @param  {string} key   键值
     * @param  {all}    value 内容
     * @return {undefined}
     */
    register(key, value) {
        if (!key || !value) {
            return;
        }
        if (this._kvMap.hasOwnProperty(key)) {
            throw new Error(`${key}已注册为${this._kvMap[key]}, 请检查！`);
        }

        this._kvMap[key] = value;
    }

    /**
     * 注销内容
     * @param {string} key 键值
     * @return {undefined}
     */
    unregister(key) {
        if (!key) {
            return;
        }
        if (!this._kvMap.hasOwnProperty(key)) {
            return;
        }

        delete this._kvMap[key];
    }

    /**
     * 根据键值获取内容
     * @param {string} key 键值
     */
    get(key) {
        return this._kvMap[key];
    }

    /**
     * 判断键值内容是否存在
     * @param key         键值
     * @returns {boolean} 是否存在
     */
    contains(key) {
        return this._kvMap.hasOwnProperty(key);
    }

    /**
     * 清空配置
     * @returns {undefined}
     */
    clear() {
        this._kvMap = {};
    }

    /**
     * 销毁单例
     * @returns {undefined}
     */
    destroy() {
        this.clear();
        super.destroy();
    }
}



