import Static from '../clazz/Static';

/**
 * 事件类型
 * 该类是一个静态类，只有静态方法，不可实例化
 */
export default class EventStore extends Static {
    static _kvMap = {};

    /**
     * 注册事件类型
     * @param  {string} key   事件类型key
     * @param  {string} value 事件类型value
     * @return {undefined}
     */
    static register(key, value) {
        if (this._kvMap.hasOwnProperty(key)) {
            // throw new Error(`${key}事件已经注册`);
            console.warn(`${key}事件已经注册`);
        }

        this._kvMap[key] = value;

        Object.defineProperty(this, key, {
            get: () => this._kvMap[key],
            enumerable: true,
            configurable: true
        });
    }

    /**
     * 注册多个事件类型
     * @param  {object} kvMap 事件类型的key/value对象
     * @return {undefined}
     */
    static registerAll(kvMap) {
        for (const key in kvMap) {
            if (kvMap.hasOwnProperty(key)) {
                this.register(key, kvMap[key]);
            }
        }
    }

    /**
     * 判断事件名称是否注册
     * @param  {string} value 事件名称
     * @return {boolean} 是否注册
     */
    static contains(value) {
        for (const key in this) {
            if (this._kvMap.hasOwnProperty(key)) {
                if (this[key] === value) {
                    return true;
                }
            }
        }

        return false;
    }

    /**
     * 清空所有注册的事件
     * @return {undefined}
     */
    static clear() {
        for (const key in this._kvMap) {
            if (this._kvMap.hasOwnProperty(key)) {
                delete this[key];
            }
        }

        this._kvMap = {};
    }
}



