import Static from '../clazz/Static';
import Encryptor from './Encryptor';

export default class BrowserStore extends Static {
    static hasLocalStorageItem(key) {
        const value = localStorage.getItem(key);
        return value !== null;
    }

    static getLocalStorageItem(key) {
        const value = localStorage.getItem(key);
        if (!value) {
            return null;
        }

        const encryptor = Encryptor.getInstance();
        const res = encryptor.decrypt(value);
        if (!res) {
            return null;
        }

        return JSON.parse(res);
    }

    static setLocalStorageItem(key, value) {
        const encryptor = Encryptor.getInstance();
        const res = encryptor.encrypt(JSON.stringify(value));
        if (!res) {
            return;
        }

        localStorage.setItem(key, res);
    }

    static removeLocalStorageItem(key) {
        localStorage.removeItem(key);
    }

    static clearLocalStorage() {
        localStorage.clear();
    }

    static hasSessionStorageItem(key) {
        const value = sessionStorage.getItem(key);
        return value !== null;
    }

    static getSessionStorageItem(key) {
        const value = sessionStorage.getItem(key);
        if (!value) {
            return null;
        }

        const encryptor = Encryptor.getInstance();
        const res = encryptor.decrypt(value);
        if (!res) {
            return null;
        }

        return JSON.parse(res);
    }

    static setSessionStorageItem(key, value) {
        const encryptor = Encryptor.getInstance();
        const res = encryptor.encrypt(JSON.stringify(value));
        if (!res) {
            return;
        }

        sessionStorage.setItem(key, res);
    }

    static removeSessionStorageItem(key) {
        sessionStorage.removeItem(key);
    }

    static clearSessionStorage() {
        sessionStorage.clear();
    }
}



